import React, { useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  Container, 
  Typography, 
  Button, 
  Box,
  Paper,
  Card,
  CardContent,
  CardActions,
  Grid,
  RadioGroup,
  FormControlLabel,
  Radio,
  FormControl,
  FormLabel,
  ToggleButtonGroup,
  ToggleButton
} from '@mui/material';
import AssignmentIcon from '@mui/icons-material/Assignment';
import AssessmentIcon from '@mui/icons-material/Assessment';
import useGTM from '../utils/useGTM';

const HomePage: React.FC = () => {
  const navigate = useNavigate();
  const { trackButtonClick, trackPageView } = useGTM();
  const [selectedQuestionCount, setSelectedQuestionCount] = React.useState<number>(25);
  const [selectedCertification, setSelectedCertification] = React.useState<string>('AD0-E213');

  //   useCallback 
  const trackHomePageView = useCallback(() => {
    trackPageView({
      page_name: '/',
      page_title: 'Adobe Analytics   '
    });
  }, [trackPageView]);

  useEffect(() => {
    console.log(' .');
    //   
    document.title = 'Adobe Analytics   ';
    
    // GTM  
    trackHomePageView();
  }, [trackHomePageView]);

  useEffect(() => {
    //     
    const certInfo = getCertificationInfo(selectedCertification);
    document.title = `[${selectedCertification}] ${certInfo.title}   `;
  }, [selectedCertification]);

  const handleQuestionCountChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSelectedQuestionCount(Number(event.target.value));
  };

  const handleCertificationChange = (event: React.MouseEvent<HTMLElement>, newCertification: string) => {
    if (newCertification !== null) {
      setSelectedCertification(newCertification);
      
      // GTM   
      trackButtonClick({
        name: 'certification_change',
        category: 'selection',
        location: 'homepage',
        additionalData: {
          certification: newCertification
        }
      });
    }
  };

  const handleStartQuiz = () => {
    // GTM     
    trackButtonClick({
      name: 'start_quiz',
      category: 'navigation',
      location: 'homepage',
      additionalData: {
        question_count: selectedQuestionCount,
        certification: selectedCertification
      }
    });
    
    navigate('/quiz', { 
      state: { 
        questionCount: selectedQuestionCount,
        certification: selectedCertification
      } 
    });
  };

  const handleViewResults = () => {
    // GTM     
    trackButtonClick({
      name: 'view_results',
      category: 'navigation',
      location: 'homepage'
    });
    
    navigate('/results');
  };

  const getCertificationInfo = (cert: string) => {
    switch (cert) {
      case 'AD0-E213':
        return {
          title: 'Adobe Analytics Developer Professional',
          description: 'Adobe Analytics     ',
          focus: ', ,  '
        };
      case 'AD0-E209':
        return {
          title: 'Adobe Analytics Developer Expert',
          description: 'Adobe Analytics   () ',
          focus: ' ,  , API,  '
        };
      case 'AD0-E208':
        return {
          title: 'Adobe Analytics Business Practitioner Expert',
          description: 'Adobe Analytics    ',
          focus: ' ,  ,  ,  '
        };
      default:
        return {
          title: '',
          description: '',
          focus: ''
        };
    }
  };

  const certInfo = getCertificationInfo(selectedCertification);

  return (
    <Container maxWidth="md">
      <Box sx={{ mt: 4, textAlign: 'center' }}>
        <Typography variant="h4" gutterBottom>
          Adobe Analytics   
        </Typography>
        
        <Card sx={{ mt: 4, mb: 4 }}>
          <CardContent>
            <Typography variant="h6" gutterBottom sx={{ mb: 3 }}>
               
            </Typography>
            
            <ToggleButtonGroup
              value={selectedCertification}
              exclusive
              onChange={handleCertificationChange}
              aria-label=" "
              sx={{ mb: 3 }}
            >
              <ToggleButton value="AD0-E213" aria-label="AD0-E213" sx={{ minWidth: 200 }}>
                <Box sx={{ textAlign: 'center', p: 1 }}>
                  <Typography variant="subtitle1" fontWeight="bold">
                    AD0-E213
                  </Typography>
                  <Typography variant="caption" display="block">
                    Developer Professional
                  </Typography>
                </Box>
              </ToggleButton>
              <ToggleButton value="AD0-E209" aria-label="AD0-E209" sx={{ minWidth: 200 }}>
                <Box sx={{ textAlign: 'center', p: 1 }}>
                  <Typography variant="subtitle1" fontWeight="bold">
                    AD0-E209
                  </Typography>
                  <Typography variant="caption" display="block">
                    Developer Expert
                  </Typography>
                </Box>
              </ToggleButton>
              <ToggleButton value="AD0-E208" aria-label="AD0-E208" sx={{ minWidth: 200 }}>
                <Box sx={{ textAlign: 'center', p: 1 }}>
                  <Typography variant="subtitle1" fontWeight="bold">
                    AD0-E208
                  </Typography>
                  <Typography variant="caption" display="block">
                    Business Practitioner Expert
                  </Typography>
                </Box>
              </ToggleButton>
            </ToggleButtonGroup>

            <Box sx={{ mb: 3, p: 2, bgcolor: 'grey.50', borderRadius: 1 }}>
              <Typography variant="h6" color="primary" gutterBottom>
                {certInfo.title}
              </Typography>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                {certInfo.description}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                <strong> :</strong> {certInfo.focus}
              </Typography>
            </Box>

            <FormControl component="fieldset">
              <FormLabel component="legend">  </FormLabel>
              <RadioGroup
                value={selectedQuestionCount}
                onChange={handleQuestionCountChange}
                row
                sx={{ justifyContent: 'center', mt: 2 }}
              >
                <FormControlLabel 
                  value={5} 
                  control={<Radio />} 
                  label="5 (7)" 
                />
                <FormControlLabel 
                  value={25} 
                  control={<Radio />} 
                  label="25 (30)" 
                />
                <FormControlLabel 
                  value={50} 
                  control={<Radio />} 
                  label="50 (60)" 
                />
              </RadioGroup>
            </FormControl>
          </CardContent>
          <CardActions sx={{ justifyContent: 'center', pb: 3 }}>
            <Button 
              variant="contained" 
              color="primary" 
              size="large"
              onClick={handleStartQuiz}
            >
               
            </Button>
          </CardActions>
        </Card>

        <Button 
          variant="outlined" 
          onClick={handleViewResults}
          sx={{ mt: 2 }}
        >
            
        </Button>
      </Box>

      <Box sx={{ mt: 6, mb: 4, textAlign: 'center' }}>
        <Typography variant="caption" color="text.secondary" display="block">
           Disclaimer: AI-generated practice questions for study only  not real Adobe exam items.
          <br />
          <a
            href="https://study-6bt.pages.dev/"
            target="_blank"
            rel="noopener noreferrer"
            className="rainbow-link"
          >
             Newsletter
          </a>
        </Typography>
     </Box>  

    </Container>
  );
};

export default HomePage; 