import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Radio,
  RadioGroup,
  FormControlLabel,
  Button,
  Paper,
  LinearProgress,
  
  Checkbox,
  FormGroup,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions
} from '@mui/material';
import { questionsAD0E213 } from '../data/questionsAD0E213';
import { questionsAD0E209 } from '../data/questionsAD0E209';
import { questionsAD0E208 } from '../data/questionsAD0E208';
import { getRandomQuestions, shuffleOptions, convertAnswer } from '../utils/quizUtils';

const QuizPage: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const questionCount = location.state?.questionCount || 25;
  const certification = location.state?.certification || 'AD0-E213';
  
  //  
  const getTimeLimit = (count: number) => {
    switch (count) {
      case 5: return 7 * 60; // 7
      case 50: return 60 * 60; // 60
      default: return 30 * 60; // 30
    }
  };

  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [selectedAnswers, setSelectedAnswers] = useState<string[]>([]);
  const [showExplanation, setShowExplanation] = useState(false);
  const [timeLeft, setTimeLeft] = useState(getTimeLimit(questionCount));
  const [isTimeUp, setIsTimeUp] = useState(false);
  const [showConfirmDialog, setShowConfirmDialog] = useState(false);
  const [quizQuestions] = useState(() => {
    let allQuestions;
    switch (certification) {
      case 'AD0-E213':
        allQuestions = questionsAD0E213;
        break;
      case 'AD0-E209':
        allQuestions = questionsAD0E209;
        break;
      case 'AD0-E208':
        allQuestions = questionsAD0E208;
        break;
      default:
        allQuestions = questionsAD0E213;
    }
    const selectedQuestions = getRandomQuestions(allQuestions, questionCount);
    return selectedQuestions.map(q => ({
      ...q,
      shuffledOptions: shuffleOptions(q.options),
      shuffledAnswer: convertAnswer(q.answer, shuffleOptions(q.options))
    }));
  });
  const [userAnswers, setUserAnswers] = useState<{ [key: number]: string[] }>({});

  const currentQuestion = quizQuestions[currentQuestionIndex];
  
  //    (  )
  const headerTitle = (() => {
    switch (certification) {
      case 'AD0-E213':
        return 'Adobe Analytics Developer Professional';
      case 'AD0-E209':
        return 'Adobe Analytics Developer Expert';
      case 'AD0-E208':
        return 'Adobe Analytics Business Practitioner Expert';
      default:
        return 'Adobe Analytics';
    }
  })();

  useEffect(() => {
    //      
    let certTitle = '';
    switch (certification) {
      case 'AD0-E213':
        certTitle = 'Adobe Analytics Developer Professional';
        break;
      case 'AD0-E209':
        certTitle = 'Adobe Analytics Developer Expert';
        break;
      case 'AD0-E208':
        certTitle = 'Adobe Analytics Business Practitioner Expert';
        break;
      default:
        certTitle = 'Adobe Analytics';
    }
    document.title = `[${certification}] ${certTitle}  -  ${currentQuestionIndex + 1}/${quizQuestions.length}`;
  }, [certification, currentQuestionIndex, quizQuestions.length]);

  useEffect(() => {
    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          clearInterval(timer);
          setIsTimeUp(true);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const handleAnswerChange = (value: string) => {
    let newAnswers: string[];
    
    if (Array.isArray(currentQuestion.shuffledAnswer)) {
      //    ()
      newAnswers = selectedAnswers.includes(value)
        ? selectedAnswers.filter(v => v !== value)
        : [...selectedAnswers, value];
    } else {
      //    ( )
      newAnswers = [value];
    }
    
    setSelectedAnswers(newAnswers);
    setUserAnswers(prev => ({
      ...prev,
      [currentQuestionIndex]: newAnswers
    }));
  };

  const handleNextQuestion = () => {
    if (currentQuestionIndex < quizQuestions.length - 1) {
      setCurrentQuestionIndex(prev => prev + 1);
      setSelectedAnswers(userAnswers[currentQuestionIndex + 1] || []);
      setShowExplanation(false);
    } else {
      setShowConfirmDialog(true);
    }
  };

  const handlePrevQuestion = () => {
    if (currentQuestionIndex > 0) {
      setCurrentQuestionIndex(prev => prev - 1);
      setSelectedAnswers(userAnswers[currentQuestionIndex - 1] || []);
    }
  };

  const handleFinish = () => {
    const results = {
      totalQuestions: quizQuestions.length,
      answeredQuestions: Object.keys(userAnswers).length,
      correctAnswers: quizQuestions.reduce((count, q, index) => {
        const userAnswer = userAnswers[index]?.sort().join(',') || '';
        const correctAnswer = Array.isArray(q.shuffledAnswer) 
          ? q.shuffledAnswer.sort().join(',')
          : q.shuffledAnswer;
        return count + (userAnswer === correctAnswer ? 1 : 0);
      }, 0),
      timeSpent: getTimeLimit(questionCount) - timeLeft,
      certification: certification,
      questions: quizQuestions.map((q, index) => ({
        question: q.question,
        userAnswer: userAnswers[index] || [],
        correctAnswer: q.shuffledAnswer,
        isCorrect: (userAnswers[index]?.sort().join(',') || '') === (
          Array.isArray(q.shuffledAnswer) 
            ? q.shuffledAnswer.sort().join(',')
            : q.shuffledAnswer
        ),
        explanation: q.explanation,
        shuffledOptions: q.shuffledOptions
      }))
    };

    localStorage.setItem('quizResults', JSON.stringify(results));
    navigate('/results');
  };

  const formatTime = (seconds: number) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes}:${remainingSeconds.toString().padStart(2, '0')}`;
  };

  if (isTimeUp) {
    return (
      <Container maxWidth="md">
        <Box sx={{ mt: 4, textAlign: 'center' }}>
          <Typography variant="h4" gutterBottom>
             !
          </Typography>
          <Button variant="contained" onClick={handleFinish}>
             
          </Button>
        </Box>
      </Container>
    );
  }

  return (
    <Container maxWidth="md">
      <Box sx={{ mt: 4 }}>
        <Box sx={{ textAlign: 'center', mb: 3 }}>
          <Typography variant="h5" gutterBottom>
            {headerTitle}
          </Typography>
          <Typography variant="subtitle1" color="text.secondary">
            {certification} 
          </Typography>
        </Box>
        
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
          <Typography variant="h6">
             {currentQuestionIndex + 1} / {quizQuestions.length}
          </Typography>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
            <Typography variant="h6" color={timeLeft < 300 ? 'error' : 'inherit'}>
               : {formatTime(timeLeft)}
            </Typography>
            <Button
              variant="outlined"
              color="secondary"
              size="small"
              onClick={() => navigate('/')}
            >
               
            </Button>
          </Box>
        </Box>
        
        <LinearProgress 
          variant="determinate" 
          value={(currentQuestionIndex + 1) / quizQuestions.length * 100} 
          sx={{ mb: 3 }}
        />

        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            {currentQuestion.question}
          </Typography>

          {Array.isArray(currentQuestion.shuffledAnswer) ? (
            //     
            <FormGroup>
              {Object.entries(currentQuestion.shuffledOptions || {}).map(([key, value]) => (
                <FormControlLabel
                  key={key}
                  control={
                    <Checkbox
                      checked={selectedAnswers.includes(key)}
                      onChange={() => handleAnswerChange(key)}
                    />
                  }
                  label={value as string}
                />
              ))}
            </FormGroup>
          ) : (
            //      
            <RadioGroup
              value={selectedAnswers[0] || ''}
              onChange={(e) => handleAnswerChange(e.target.value)}
            >
              {Object.entries(currentQuestion.shuffledOptions || {}).map(([key, value]) => (
                <FormControlLabel
                  key={key}
                  value={key}
                  control={<Radio />}
                  label={value as string}
                />
              ))}
            </RadioGroup>
          )}

          {showExplanation && (
            <Alert severity="info" sx={{ mt: 2 }}>
              <Typography variant="body2" sx={{ whiteSpace: 'pre-wrap' }}>
                {currentQuestion.explanation}
              </Typography>
            </Alert>
          )}
        </Paper>

        <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
          <Box>
            <Button
              variant="outlined"
              onClick={handlePrevQuestion}
              disabled={currentQuestionIndex === 0}
              sx={{ mr: 1 }}
            >
              
            </Button>
            <Button
              variant="outlined"
              onClick={() => setShowExplanation(!showExplanation)}
            >
              {showExplanation ? ' ' : ' '}
            </Button>
          </Box>
          <Button
            variant="contained"
            onClick={handleNextQuestion}
            disabled={selectedAnswers.length === 0}
          >
            {currentQuestionIndex === quizQuestions.length - 1 ? '' : ''}
          </Button>
        </Box>
      </Box>

      <Dialog
        open={showConfirmDialog}
        onClose={() => setShowConfirmDialog(false)}
      >
        <DialogTitle> </DialogTitle>
        <DialogContent>
          <Typography>
             ?
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowConfirmDialog(false)}>
            
          </Button>
          <Button onClick={handleFinish} color="primary">
            
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default QuizPage; 